@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,900;1,100&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,300;0,600;0,700;0,800;0,900;1,100;1,800&display=swap");
:root {
  --color-one: rgba(225, 0, 0);
  --color-one-light: rgba(225, 0, 0, 0.1);
  --background: #000;
  --text-color: 255, 138, 138;
}
* {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  -webkit-tap-highlight-color: transparent;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  user-select: none;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
body {
  padding: 0;
  margin: 0;
  background: var(--background);
  color: rgba(var(--text-color), 0.891);
}
.introSub {
  color: rgba(var(--text-color), 0.891);
  font-size: 27px;
}
.introSub strong {
  color: var(--color-one);
  border: 1px solid rgb(255 255 255 / 4%);
  padding: 5px 4px;
  margin: 0px 0px;
  border-radius: 5px;
  background: rgb(255 255 255 / 2%);
}
@media only screen and (min-device-width: 768px) {
  .margin {
    padding: 0px 20vw;
  }
}
@media only screen and (min-device-width: 168px) {
  .margin {
    padding: 0px 10vw;
  }
}
#color {
  bottom: 86px;
  position: fixed;
  left: 6px;
  z-index: 6;
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 74px;
  position: fixed;
  width: 100%;
  margin: auto 0;
  z-index: 5;
  border-bottom: 1px solid var(--color-one);
  padding: 0 10vw;
  box-sizing: border-box;
}
.title {
  font-size: 1.4em;
  font-weight: 100;
  letter-spacing: 3vw;
}
.boxes {
  margin: 0 0 0px 0;
  display: grid;
  grid-gap: 19px 10px;
  grid-template-columns: repeat(4, auto);
  /* grid-template-rows: repeat(3, max-content); */
  grid-template-rows: auto 1fr; /* NEW */
  justify-content: center;
  align-content: end;
}
.boxStyle .boxStyle-head {
  padding: 15px 10px 0px 10px;
  margin: 5px;
}
.boxStyle .boxStyle-body {
  padding: 0px 10px 10px 10px;
  margin: 5px;
}
.boxStyle .boxStyle-item {
  border: 1px solid var(--color-one);
  padding: 15px;
  margin: 0px;
  font-size: 16px;
  border-left: 0;
  border-right: 0;
  border-radius: 0;
  border-bottom: 0;
  /* min-height: 10%; */
}
.boxStyle .boxStyle-item.last {
  border-bottom: 1px solid var(--color-one);
}
.backdrop {
  background-image: url("https://www.codecademy.com/webpack/ecd7bf95c62f994b728de4dc976721df.png");

  backdrop-filter: blur(5px) brightness(150%) saturate(200%) hue-rotate(-30deg);
}
.backdropNav {
  backdrop-filter: blur(5px) brightness(50%) saturate(200%) hue-rotate(-30deg);
  background: linear-gradient(
    90deg,
    rgb(0 0 0 / 0%) 25%,
    rgba(var(--color-one), 0.5) 50%,
    rgb(0 0 0 / 0%) 79%
  );
  background-size: 460%;
  animation: backdropnav 2.5s cubic-bezier(1, 0, 0.35, 1.08) 0.41s infinite
    normal;
  background-image: url("https://www.codecademy.com/webpack/ecd7bf95c62f994b728de4dc976721df.png");
}
@keyframes backdropnav {
  0% {
    background-position: -29%;
  }
  100% {
    background-position: -127%;
  }
}
.boxStyle {
  color: rgba(var(--text-color), 0.891);
  font-size: 20px;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid var(--color-one);
  background: var(--color-one-light);
  min-height: 28vh;
  transition: opacity 2.6s;
}
.is-inview {
  transform: scale(1);
  opacity: 1;
  animation: inview 0.5s cubic-bezier(1, 0, 0.35, 1.08) 0s 1 normal;
}
@keyframes inview {
  0% {
    scale: 0.3;
  }
  100% {
    scale: 1;
  }
}
.boxes .boxStyle:nth-child(1) {
  grid-column: 1 / 5;
}

.boxes .boxStyle:nth-child(2) {
  grid-column: 1 / 3;
  grid-row: 2 / 4;
  max-height: fit-content;
}

.boxes .boxStyle:nth-child(3) {
  grid-column: 3 / 5;
}
.boxes .boxStyle:nth-child(4) {
  grid-column: 3 / 5;
}
/* .div {
  padding: 0px 8vw;
} */
.Home {
  font-size: 50px;
}
.btn .icon svg {
  position: absolute;
  height: 78%;
  width: 49%;
  top: 4px;
  right: -5px;
  opacity: 0.43;
}
.navbar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 80px;
  flex-direction: row;
  flex-wrap: nowrap;
  position: fixed;
  bottom: 0px;
  width: 100%;
  margin: auto 0;
  z-index: 5;
  border-top: 1px solid var(--color-one);
}

.active .btn {
  background-color: var(--color-one-light);
  box-shadow: 1px 1px 0px 0px var(--color-one);
  transform: translate(0px, 0px);
}

.btn {
  cursor: pointer;
  background-color: var(--color-one-light);
  color: rgba(var(--text-color), 0.891);
  padding: 11px 33px 11px 15px;
  transition: all 0.08s;
  border-radius: 3px;
  user-select: none;
  border: none;
  margin: 0px 10px;
  box-shadow: 4px 4px 0 0px var(--color-one);
  transform: translate(-3px, -3px);
  border: 1px solid var(--color-one);
}
.introHead {
  font-family: "Raleway", sans-serif;
  font-weight: 100;
  font-size: 60px;
  margin: 0vh 0 2.6vh 0px;
}
.btnNav {
  padding: 10px 20px;
}
.btn:active {
  background-color: var(--color-one-light);
  box-shadow: 0px 0px 0 0px var(--color-one);
  transform: translate(0px, 0px);
}
.intro {
  padding: 20px 30px;
}
.boxStyle-head {
  font-size: 30px;
  margin-bottom: 9px;
}
.boxStyle-body {
  font-size: 15px;
}
.boxStyle-icon {
  position: static;
  height: 52px;
}
.boxStyle-icon svg {
  position: absolute;
  bottom: 12px;
  right: 4px;
  height: 29px;
  width: 11vw;
  height: 35px;
  width: 46px;
  opacity: 0.71;
  color: var(--color-one);
}

.footer-wrap {
  border-top: 1px solid var(--color-one);
  padding: 14px;
}
.footer {
  margin: 10px;
  /* display: grid; */
  grid-gap: 2vw;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-template-rows: repeat(2, 15vh);
  justify-content: center;
  align-content: end;
}
.felem {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 300px 300px;
  padding: 10px;
  border-top: 1px solid var(--color-one);
}

/* Background begin */

#backWrap {
  position: fixed;
  height: 100vh;
  top: 0;
  left: 0;
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-around;
  align-items: center;
  z-index: -1;
}
#backWrap div {
  position: absolute;
  border: 1px solid var(--color-one);
  border-radius: 0px 0px;
  opacity: 0;
  transform: translate(0px, 0px) scale(3);
}
#backWrap div:nth-child(1) {
  background-color: var(--color-one-light);
  height: 200px;
  width: 300px;
  animation: back 6s cubic-bezier(1, 0, 0.35, 1.08) 0.41s infinite
    alternate-reverse;
}
#backWrap div:nth-child(2) {
  background-color: var(--color-one-light);
  height: 200px;
  width: 200px;
  animation: back 4s cubic-bezier(1, 0, 0.35, 1.08) 0.62s infinite
    alternate-reverse;
}
#backWrap div:nth-child(3) {
  background-color: var(--color-one-light);
  height: 150px;
  width: 350px;
  animation: back 4s cubic-bezier(1, 0, 0.35, 1.08) 0.83s infinite
    alternate-reverse;
}
#backWrap div:nth-child(4) {
  background-color: var(--color-one-light);
  height: 120px;
  width: 120px;
  animation: back 5s cubic-bezier(1, 0, 0.35, 1.08) 1.1s infinite
    alternate-reverse;
}
#backWrap div:nth-child(5) {
  background-color: var(--color-one-light);
  height: 50px;
  width: 50px;
  animation: back 1s cubic-bezier(1, 0, 0.35, 1.08) 1.5s infinite
    alternate-reverse;
}
#backWrap div:nth-child(6) {
  background-color: var(--color-one-light);
  height: 50px;
  width: 140px;
  animation: back 3s cubic-bezier(1, 0, 0.35, 1.08) 1.6s infinite
    alternate-reverse;
}
#backWrap div:nth-child(7) {
  background-color: var(--color-one-light);
  height: 250px;
  width: 250px;
  animation: back 3s cubic-bezier(1, 0, 0.35, 1.08) 1.3s infinite
    alternate-reverse;
}
@keyframes back {
  0% {
    border-radius: 0px 0px;
    opacity: 0.21;
    transform: translate(0px, 0px) scale(1);
  }
  30% {
    border-radius: 10px 10px;
    opacity: 0.21;
    transform: translate(0px, 00px) scale(0.92);
  }
  40% {
    border-radius: 10px 200px;
    opacity: 0.21;
    filter: hue-rotate(-68deg);
    transform: translate(10px, 10px) scale(1);
  }
  60% {
    border-radius: 10px 10px;
    transform: translate(5px, -10px) scale(1);
    opacity: 0.61;
  }
  70% {
    border-radius: 10px 10px;
    opacity: 0.21;
    transform: translate(0px, -00px) scale(0.9);
    filter: hue-rotate(38deg);
  }
  80% {
    border-radius: 200px 100px;
    opacity: 0.21;
    transform: translate(0px, 10px) scale(1);
  }
  100% {
    border-radius: 0px 0px;
    opacity: 0;
    transform: translate(0px, 0px) scale(1);
  }
}

/* Background end */

/* Logo start */

#logo1,
#logo2,
#logo3 {
  border: 1px solid var(--color-one);
  display: block;
  height: 3px;
  content: "";
  transform: scale(1);
  opacity: 0.9;
}
#logo1 {
  width: 13px;
  transform: translate(0px, -4px);
}
#logo2 {
  width: 25px;
  transform: translate(0px, 0px);
}
#logo3 {
  width: 36px;
  transform: translate(0px, 4px);
}

/* Logo end */

/* SliderItem start */
.Slider {
  overflow: hidden;
}
.slider-items {
  font-size: 20px;
  /* display: grid;
  grid-gap: 26px 36vw;
  grid-template-columns: repeat(15, auto);*/
  overflow: visible;
  position: relative;
  /* width: 88vw; */
  height: max-content;
}
.slider-item {
  width: 100px;
  height: 97%;
  width: 69%;
  will-change: transform;
  position: absolute;
  height: inherit;
  z-index: -1;
  transform: translate(150vw, 0px);
  transition: all cubic-bezier(0.76, 0.18, 0.17, 0.97) 0.65s;
}
.slider-item.slight-right,
.slider-item.slight-left {
  transition: all cubic-bezier(0.97, 0.04, 0.17, 0.97) 0.65s;
  opacity: 0.4;
  /* filter: blur(3px); */
}
.slider-body,
.slider-pic {
  transition: all cubic-bezier(0, 0.51, 0.24, 0.97) 0.75s 0.23s;
  background-color: var(--color-one-light);
  /* background-color: #fff; */
  background-image: url("https://www.codecademy.com/webpack/ecd7bf95c62f994b728de4dc976721df.png");
  border: 1px solid var(--color-one);
  /* filter: blur(2px); */
  min-height: 50%;
  transform: translate(0vw, -10%) scale(0.9);
}
.slider-item .slider-body {
  padding: 10px 5px 50px 5px;
  transform: translate(-0vw, 0px) scale(0.9);
}
.slider-item .slider-pic {
  transform: translate(-5vw, -15%) scale(0.9);
  height: 60%;
  width: 90%;
  position: absolute;
  right: 0;
  background: url("http://placeimg.com/640/360/any") 50% 50% / cover no-repeat;
}
.slider-item.current-item .slider-body {
  filter: blur(0px);
  transform: translate(-4vw, 0px);
}
.slider-item.current-item .slider-pic {
  filter: blur(0px);
  transform: translate(-17vw, -30%);
}
.slider-item.current-item {
  transform: translate(20vw, 0px);
  z-index: 1;
}
.slider-item.left {
  transform: translate(-150vw, 0px);
}
.slider-item.slight-right {
  transform: translate(70vw, 0px);
}
.slider-item.slight-left {
  transform: translate(-30vw, 0px);
}

.Slider button {
  border: 1px solid var(--color-one);
  background-color: transparent;
  color: var(--color-one);
  font-size: 25px;
  line-height: 0px;
  padding: 10px 4vw;
  margin: 10px 10px 0px 0px;
  box-shadow: 4px 4px 0px 0 var(--color-one);
  transition: all 0.051s;
}
.button-grp {
  z-index: 2;
  position: relative;
  float: right;
  right: 10vw;
  /* padding:30px; */
}
.Slider button:active {
  background: var(--color-one);
  box-shadow: 2px 2px 0px 0px var(--color-one);
}

/* SliderItem end */
